import { createSlice } from "@reduxjs/toolkit";
namespace ToastrStore {
  type ToastrStoreType = {};
  const initialState: ToastrStoreType = {};
  const toastrStoreSlice = createSlice({
    initialState,
    name: "toastr",
    reducers: {},
  });
  export const actions = toastrStoreSlice.actions;
  export const reducer = toastrStoreSlice.reducer;
  export const select = {};
}

export default ToastrStore;
